.takeAwayItem {
    width: 600px;
    height: 1000px;
    background: linear-gradient(0deg, rgba(247, 0, 255, 0.815), rgba(225, 0, 255, 0.212));
    border-radius: 30px;
}

.takeAwayItem>h1 {
    font-size: 40px;
}

.takeAway span {
    font-size: 20px;
    font-weight: 600;
    color: wheat;
    text-shadow: 2px 2px 1px black;
}

.marginSpan {
    margin: 20px;
}

.order {
    display: inline-block;
    margin-right: 400px;
}

.takeAwayItem {
    display: inline-block;
    position: absolute;
    right: 0px;
}

.burger {
    background-color: rgba(245, 222, 179, 0.575);
    border-radius: 45px;
    border: 1px black solid;
}

.burger>h1 {
    display: block;
    font-size: 30px;
    margin-bottom: 0px;
}

.burger>h3 {
    margin-top: 0px;
    font-size: 30px;
    color: white;
    text-shadow: 2px 2px 1px black;
}

.burger>button,
.burger>div>button {
    margin: 15px;
    background-color: wheat;
    border: 1px black solid;
    border-radius: 10px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.cheeseBurger {
    background-color: rgba(245, 222, 179, 0.575);
    border-radius: 45px;
    border: 1px black solid;
}

.cheeseBurger>h1 {
    display: block;
    font-size: 30px;
    margin-bottom: 0px;
}

.cheeseBurger>h3 {
    margin-top: 0px;
    font-size: 30px;
    color: white;
    text-shadow: 2px 2px 1px black;
}

.cheeseBurger>button {
    margin: 15px;
    background-color: wheat;
    border: 1px black solid;
    border-radius: 10px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.topping {
    color: white;
    text-shadow: 2px 2px 1px black;
    font-weight: 600;
    font-family: quoteText;
}

@media (max-width: 1000px) {
    .order {
        position: relative;
        display: block;
        margin-right: 0px;
    }
    .burger>span,
    .cheeseBurger>span {
        display: block;
    }
}